// components/home-components/EventsGrid/index.js
import React from "react";
import Image from "next/image";
import { FiArrowRightCircle } from "react-icons/fi";
import styles from "./EventsGrid.module.css";

export default function EventsGrid() {
  const events = [
    {
      id: "featured-1",
      type: "UPCOMING_EVENTS",
      title: 'TECHTONIC SUMMIT: IDEAS THAT SHAKE THE FUTURE"',
      bgColor: "#4A4A4A",
      layout: "LARGE_TOP_LEFT",
      img: "/images/home-page/seven-first-banner.png",
    },
    {
      id: "event-1",
      type: "EVENT",
      title: "Annual fest that celebrates everything JSS stands for",
      date: "August 16, 2024",
      bgColor: "#C84B3A",
      layout: "SMALL",
    },
    {
      id: "news-1",
      type: "NEWS",
      title: "SmashZone League: The Ultimate Badminton Battle",
      date: "October 04, 2024",
      bgColor: "#9B9B9B",
      img: "/images/home-page/seven-dummy-img.png",
      layout: "SMALL_WITH_IMAGE",
    },
    {
      id: "event-2",
      type: "EVENT",
      title: "Smart Energy Council: Powering Australia's Renewable Future",
      date: "October 11, 2024",
      bgColor: "#FFFFFF",
      img: "/images/home-page/seven-first-logo.png",
      layout: "SMALL_WITH_LOGO",
    },
    {
      id: "image-1",
      type: "IMAGE_ONLY",
      img: "/images/home-page/seven-dummy-img.png",
      alt: "Event logo",
      bgColor: "#4A4A4A",
      layout: "SMALL_IMAGE_ONLY",
    },
    {
      id: "featured-2",
      type: "UPCOMING_EVENTS",
      title: "SUMMER BEATS FESTIVAL 2025",
      subtitle:
        "A fusion of tech and music, featuring DJ sets and app showcases",
      bgColor: "#4A4A4A",
      layout: "LARGE_BOTTOM_RIGHT",
      img: "/images/home-page/seven-last-banner.png",
    },
    {
      id: "image-2",
      type: "IMAGE_ONLY",
      img: "/images/home-page/seven-dummy-img.png",
      alt: "Event logo",
      bgColor: "#4A4A4A",
      layout: "SMALL_IMAGE_ONLY",
    },
    {
      id: "event-3",
      type: "EVENT",
      title: "Business Today CODESTORM 2.0",
      subtitle: "A tech showdown with coding battles and hackathons",
      date: "October 16, 2024",
      bgColor: "#2B5DAA",
      img: "/images/home-page/seven-second-logo.png",
      layout: "SMALL_WITH_SUBTITLE",
    },
    {
      id: "event-4",
      type: "EVENT",
      subtitle: "Hosts the 18th International Olympiad on Astronomy",
      date: "October 16, 2024",
      bgColor: "#fff",
      img: "/images/home-page/seven-third-logo.png",
      layout: "SMALL_WITH_PARTNER",
    },
  ];

  // Render event card based on layout type
  const renderEventCard = (event) => {
    switch (event.layout) {
      case "LARGE_TOP_LEFT":
        return (
          <div
            key={event.id}
            className={`${styles.card} ${styles.cardLarge} ${styles.cardLargeTopLeft}`}
            style={{ backgroundColor: event.bgColor }}
          >
            <Image
              src={event.img}
              alt={event.title}
              fill
              className={styles.cardImage}
            />
            <div className={styles.contentPart}>
              <div className={styles.orangeLine}></div>
              <p className={styles.cardTag}>{event.type.replace("_", " ")}</p>
              <h2 className={styles.cardTitleLarge}>{event.title}</h2>
              <FiArrowRightCircle color="#fff" fontSize={20} />
            </div>
          </div>
        );

      case "LARGE_BOTTOM_RIGHT":
        return (
          <div
            key={event.id}
            className={`${styles.card} ${styles.cardLarge} ${styles.cardLargeBottomRight}`}
            style={{ backgroundColor: event.bgColor }}
          >
            <Image
              src={event.img}
              alt={event.title}
              fill
              className={styles.cardImage}
            />
            <div className={styles.contentPart}>
              <div className={styles.orangeLine}></div>
              <h2 className={styles.cardTitleLarge}>{event.title}</h2>
              <p
                className={`${styles.cardSubtitle} ${styles.cardSubtitleWhite}`}
              >
                {event.subtitle}
              </p>
              <FiArrowRightCircle color="#fff" fontSize={20} />
            </div>
          </div>
        );

      case "SMALL_WITH_IMAGE":
        return (
          <div
            key={event.id}
            className={styles.card}
            style={{ backgroundColor: event.bgColor }}
          >
            <Image
              src={event.img}
              alt={event.title}
              fill
              className={styles.cardImage}
            />
            <div className={styles.miniContentPart}>
              <p className={styles.cardTag}>{event.type}</p>
              <h3 className={`${styles.cardTitle} ${styles.cardTitleWhite}`}>
                {event.title}
              </h3>
              <p className={`${styles.cardDate} ${styles.cardDateWhite}`}>
                {event.date}
              </p>
            </div>
          </div>
        );

      case "SMALL_WITH_LOGO":
        return (
          <div
            key={event.id}
            className={styles.card}
            style={{ backgroundColor: event.bgColor }}
          >
            <p className={`${styles.cardTag} ${styles.cardTagDark}`}>
              {event.type}
            </p>
            <Image
              src={event.img}
              alt={event.title}
              width={100}
              height={90}
              className={styles.cardImage}
            />
            <h3 className={styles.cardTitle}>{event.title}</h3>
            <p className={styles.cardDate}>{event.date}</p>
          </div>
        );

      case "SMALL_IMAGE_ONLY":
        return (
          <div
            key={event.id}
            className={styles.card}
            style={{ backgroundColor: event.bgColor }}
          >
            <Image
              src={event.img}
              alt={event.alt}
              fill
              className={styles.cardImage}
            />
          </div>
        );

      case "SMALL_WITH_SUBTITLE":
        return (
          <div
            key={event.id}
            className={styles.card}
            style={{ backgroundColor: event.bgColor }}
          >
            <p className={styles.cardTag}>{event.type}</p>
            <div>
              <Image
                src={event.img}
                alt={event.alt}
                height={50}
                width={200}
                className={styles.cardImage}
              />
              <p
                className={`${styles.cardSubtitle} ${styles.cardSubtitleWhite}`}
              >
                {event.subtitle}
              </p>
              <p className={`${styles.cardDate} ${styles.cardDateWhite}`}>
                {event.date}
              </p>
            </div>
          </div>
        );

      case "SMALL_WITH_PARTNER":
        return (
          <div
            key={event.id}
            className={styles.card}
            style={{ backgroundColor: event.bgColor }}
          >
            <p className={`${styles.cardTag} ${styles.cardTagDark}`}>
              {event.type}
            </p>
            <Image
              src={event.img}
              alt={event.alt}
              height={50}
              width={200}
              className={styles.cardImage}
            />
            <div className={styles.seamEduLogo}>
              <span className={styles.seamEduBold}>Knowledge Partner</span>
              <span className={styles.seamEduTitle}>
                seam<span className={styles.seamEduLight}>edu</span>
              </span>
            </div>
            <h3 className={styles.cardTitle}>{event.title}</h3>
            <p className={styles.cardDate}>{event.date}</p>
          </div>
        );

      case "SMALL":
      default:
        return (
          <div
            key={event.id}
            className={styles.card}
            style={{ backgroundColor: event.bgColor }}
          >
            <p className={styles.cardTag}>{event.type}</p>
            <h3 className={`${styles.cardTitle} ${styles.cardTitleWhite}`}>
              {event.title}
            </h3>
            <p className={`${styles.cardDate} ${styles.cardDateWhite}`}>
              {event.date}
            </p>
          </div>
        );
    }
  };

  return (
    <section className={styles.eventSection}>
      <div className={styles.container}>
        <div className={styles.header}>
          <p className={styles.headerSmall}>HAPPENINGS</p>
          <h1 className={styles.headerTitle}>
            WHAT'S <span className="blue-text">HAPPENING</span> @JSS NOIDA
          </h1>
        </div>

        <div className={styles.grid}>{events.map(renderEventCard)}</div>
      </div>
    </section>
  );
}
